import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import linkedine from '../img/linkedin.png';
import git from '../img/github.png';
function Contact (){


    return(
        <>
            <div  >
                <Container>
                <div style={{height: "50px"}}>
                    
                    </div>
                    <div style={{textAlign: "center"}}>
                        <h1 class="big">Contact</h1>
                        <h2 class="mb-4">Contact</h2>
                    </div>
                    <div style={{height: "50px"}}></div>

                    <Row>
                        <Col md={6}>
                            <p>Get in touch or shoot me an email directly on <b>joseph4jubilant@gmail.com</b></p>
                            <Form action="../../public/contact.php" method="post">
                                <Form.Group controlId="exampleForm.ControlInput1">
                                    
                                    <Form.Control type="text" name="first_name" placeholder="First name" />
                                </Form.Group><br/>

                                <Form.Group controlId="exampleForm.ControlInput1">
                                    
                                    <Form.Control type="text" name="last_name" placeholder="last name" />
                                </Form.Group><br/>
                                <Form.Group controlId="exampleForm.ControlInput1">
                                   
                                    <Form.Control type="text" name="email" placeholder="Email" />
                                </Form.Group>
                                <br />
                                <Form.Group controlId="exampleForm.ControlTextarea1">
                                   
                                    <Form.Control name="message"  as="textarea" placeholder="Message" rows={3} />
                                </Form.Group>
                                <br /><br />

                                <Button  variant="secondary">send</Button>
                            </Form>

                            <br /><br />

                            <div className="social-link-warp">
                                <div className="social-links">
                                    <a href="https://www.linkedin.com/in/joseph-chinedu-ba9a19170/"><img src={linkedine} alt="linkedin" style={{padding: '20px'}} /></a>
                                    <a href="https://github.com/Josephchinedu"><img src={git} alt="linkedin" style={{width: '30px'}} /></a>
                                    
                                </div>
                                
                            </div>


                            <br /><br /><br /><br />
                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    )
}

export default Contact;