import React from 'react';
import Container from 'react-bootstrap/Container';
import "./About.css";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import myself from '../img/myself.png';

function About (){
    return(
        <>
            <div >
                <Container>
                    <div style={{height: '40px'}}>
                        
                    </div>
                    <div style={{textAlign: "center"}}>
                        <h1 class="big">About</h1>
                        <h2 class="mb-4">About Me</h2>
                    </div>

                    <Row>
                        <div style={{height: "40px"}}>

                        </div>
                        

                        <Col sm={6}>
                            <img src={myself} alt="" id="myself" />
                        </Col>
                        <Col sm={6}>
                            <h1>Hi, I'm Joseph</h1>
                            <p id="myselfp">I'm a software developer based in Nigeria.  I specialize in building applications 
                            specific to the business needs of my clients</p>
                            <br />
                            <ul id="self_list">
                                <li className="d-flex"><span>Name:</span> <span>Joseph Chinedu Ogbu</span></li>
                                <li className="d-flex"><span>Address:</span> <span>Lagos, Nigeria</span></li>
                                <li className="d-flex"><span>Email:</span> <span>josephjubilant@gmail.com</span></li>
                                <li className="d-flex"><span>Phone:</span> <span>+2347039115243</span></li>
                            </ul><br /><br />
                            <h1>Skills:</h1>
                            <p id="myselfp">HTML5, CSS3, JAVASCRIPT, SASS, WORDPRESS, GIT, BOOTSTRAP, HEROKU, NETLIFY, REACTJS, PYTHON / DJANGO </p>
                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    )
}

export default About;