import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Container from 'react-bootstrap/Container';
import github from '../img/github_PNG58.png';
import './Navbar css/style.css';
import {
  Link
} from "react-router-dom";
import logo from '../img/logo.PNG';

function Navmenu(){
    return(
        <>
          <Navbar id="nav_menu" bg="ligjht" expand="lg">
            <Container>
              <Navbar.Brand><Link to="/">
                <img src={logo} alt="" id="log" /> JOE
              </Link></Navbar.Brand>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="m-auto">
              <Nav.Link><Link to="/about">
                About
              </Link></Nav.Link>
              <Nav.Link id="bootstrap_link" target="_blank" href="https://josephchinedu.hashnode.dev/">Blog</Nav.Link>

              <Nav.Link><Link to="/project">
                Projects
              </Link></Nav.Link>

              <Nav.Link><Link to="/resume">
                Resume
              </Link></Nav.Link>

              <Nav.Link><Link to="/contact">
                Contact
              </Link></Nav.Link>
            
              
              </Nav>

              <span inline>
                <a href="https://github.com/Josephchinedu" rel="noreferrer" target="_blank" alt="Github">
                <img id="github" alt="" src={github} />
                </a>
              </span>
              </Navbar.Collapse>
            </Container>
          </Navbar>
        </>
    )
}

export default Navmenu;